.Tough_roof {
  display: flex;
  flex-direction: row;
  background: white;
  position: relative; /* Родитель для абсолютного позиционирования */
  overflow: visible; /* Позволяет выходить за границы */
  z-index: 2; /* Отправляем картинку на задний план */
  }

  .tiles-png {
    position: absolute;
    width: 35%;
    top: 6px;
    left: -10%;
    transform: translateY(-50%);
    z-index: 0;
    opacity: 0;
    transition: 0.5s;
  }
  
  .main_container:hover .tiles-png  {
    opacity: 1;
    transform: translateY(-10%);
  }
  
  
